html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

canvas {
  width: 100%;
  height: 100%;
}

.btn {
  color: #fff;
  width: 100px;
  text-align: center;
  box-sizing: border-box;
  text-transform: uppercase;
  cursor: pointer;
  background: #000;
  border-radius: 15px;
  padding: 15px;
  font-size: 24px;
}

.snapshot-container {
  width: 100%;
  justify-content: center;
  display: flex;
  position: absolute;
  bottom: 20px;
}

.btn:hover {
  background: red;
}

.btn:active {
  background-color: gray;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

